import React, { Component } from 'react';
import { Route,BrowserRouter as Router, Link } from 'react-router-dom';
import './Kunduz.css';
import Name from './Images/Cyrus.png';
import Over from './Kunduz/Overview.png';
import Preview from './Kunduz/preview.PNG';
import Sketch1 from './Kunduz/Sketch1.PNG';
import Sketch2 from './Kunduz/Sketch2.PNG';
import Sketch3 from './Kunduz/Sketch3.PNG';
import Sketch4 from './Kunduz/Sketch4.PNG';
import Eyetracking1 from './Kunduz/Eyetracking1.PNG';
import Eyetracking2 from './Kunduz/Eyetracking2.PNG';
import Eyetracking3 from './Kunduz/Eyetracking3.PNG';
import Eyetracking4 from './Kunduz/Eyetracking4.PNG';
import Eyetracking5 from './Kunduz/Eyetracking5.PNG';
import ReactPlayer from 'react-player';
import { Player , BigPlayButton } from 'video-react';
import "../node_modules/video-react/dist/video-react.css";
import Video from './Kunduz/VideoDemo.mp4';
import Top from './Images/Top.png';


var intro_style = {
  // backgroundImage: 'url('+Back2+')',
  // backgroundSize: 'cover',
  display: 'flex',
  flexDirection: 'column',
  minHeight: '100vh'
}
var greeting_style = {
  fontFamily: 'Montserrat'
}


class Kunduz extends Component {

  componentDidMount() {
    window.scrollTo({
      top: 0,
      left: 0,
      behavior: 'auto'
    });
  }
  render() {
    return (
    <div className = "page" style = {greeting_style}>
      <div id = "navs">
        <img id = "name" src = {Name} alt = "Cyrus Twombly Logo"/>
        <div id = "nav">
          <h2 className = "navitem" id = "About"><Link to = "/">About</Link></h2>
          <h2 className = "navitem" id = "Projects"><Link to = "/#Project-Container">Projects</Link></h2>
          <h2 className = "navitem" id = "resume" ><a href = "https://github.com/cyrustwom">Resume</a></h2>
          <h2 className = "navitem" id = "LinkedIn"><a href = "https://linkedin.com/in/cyrus-twombly-34308812b">LinkedIn</a></h2>
          <h2 className = "navitem" id = "Github"><a href = "https://github.com/cyrustwom">Github</a></h2>
        </div>
        <h1 id = "greeting"> Cyrus Twombly</h1>
      </div>

      <div id = "bod">
        <h1 className = "title"> Kunduz</h1>

        <p className = "bodytext">
          Inspired to create our own prototype of an education app, a group of fellow User Interface students and I chose to redesign Kunduz. Kunduz
          is a well-known app in Turkey that allows students to upload photos of problems they need help with.
          Once a students’ problem is submitted, a tutor solves the problem within minutes and sends their answer
          back to the student.
        </p>
        <img id = "preview" className = "imageK" src = {Preview} alt = "Log in Screen Hifi Picture"/>

        <h2 className = "section" id = "initdesign"> Initial Design </h2>

        <p className = "bodytext">
        We redesigned Kunduz from a student’s perspective, because students make up the
        majority of Kunduz’s user base. Our first decision was to maintain the mobile format.
        This way, students can easily
        take a picture of the problems they need help and directly upload that picture to their question.
         <b> Every</b> great app begins with sketches. We drafted four candidates
        </p>
        <div id = "Sketches">
        <img id = "Sketch1" className = "imageKS" src = {Sketch1} alt = "Sketch group 1"/>
        <img id = "Sketch2" className = "imageKS" src = {Sketch2} alt = "Sketch group 2"/>
        <img id = "Sketch3" className = "imageKS" src = {Sketch3} alt = "Sketch group 3"/>
        <img id = "Sketch4" className = "imageKS" src = {Sketch4} alt = "Sketch group 4"/>
        </div>
        <p className = "bodytext">
        Using those low-level sketches as inspiration, here is our hi-fi design. We used Figma to design <a href = "https://www.figma.com/file/EkrHZdz1syxSubHe29kL9BVE/Iterative_Design_Kunduz?node-id=6%3A2" className = "linktofig">this mockup.</a>

        </p>
        <div id = "playdiv">
        <Player fluid = {false} width = {800} playsInline src={Video}>
          <BigPlayButton position = "center"/>
        </Player>
        </div>
        <h2 className = "section" id = "eyetracking"> Eye Tracking </h2>
        <p className = "bodytext"> We wanted to test how well this design would enable a user to complete a task, which is to
          upload a question. First we hypothesize the upper half of the home screen (inbox page) is where our user
          will spend the most time. Second, we hypothesize that the user will indeed perform all actions in the
          correct order to achieve this task: press the login button on the login page to go to the inbox page, click on
          the camera icon on the inbox page to go to the camera view, click the take picture button in the camera
          view, and finally press the upload button to upload the picture they took, which will return them to the
          inbox page.</p>
          <div id = "Heatmaps">
        <img id = "Eyetracking1" className = "imageKE" src = {Eyetracking1} alt = "Eyetracking heatmap 1"/>
        <img id = "Eyetracking2" className = "imageKE" src = {Eyetracking2} alt = "Eyetracking heatmap 2"/>
        <img id = "Eyetracking3" className = "imageKE" src = {Eyetracking3} alt = "Eyetracking heatmap 3"/>
        <img id = "Eyetracking4" className = "imageKE" src = {Eyetracking4} alt = "Eyetracking heatmap 4"/>
        <img id = "Eyetracking5" className = "imageKE" src = {Eyetracking5} alt = "Eyetracking heatmap 5"/>
        </div>
        <p className = "bodytext">
          We tracked a user’s eye movements as she completed the task. The eyetracking results support
          our first hypothesis because the frequency at which heat map’s red spots tended to be near the top
          indicates our test user did spend most of their time on the home screen skimming the upper half of the
          inbox. Our reasoning is that our inbox-style screen resembles a typical email account, and as the most
          recent emails are displayed closest to the top, most users spend most time reading through the top few
          items in Kunduz’s inbox because they associate them with “the most recent” questions. This increases
          usability because it is not only quicker for them to locate the correct thing the first time, making it more
          learnable, but they will also associate the navigation bar with the camera sign all following satisfaction
          However, we can only partially accept our second hypothesis. We had thought that because we
          have a minimalist UI with few buttons, so that the uploading question procedure seems rather
          straightforward. In reality, while our user did eventually proceed in our hypothesized order, she tried
          clicking on the three bars icon on the mobile navigation bar before she clicked on the camera icon.
          Apparently, the purpose of the camera icon was not as apparent to users as we had expected.
        </p>
        <h2 className = "section" id = "Final"> Final Result </h2>
        <p className = "bodytext">
        You will note that the final design contains more clarity toward message status in the inbox pane. Also, the question view elucidates the description, which it did not before.
        Overall, the design worked well for the purpose of an initial redesign and mobile expansion of Kunduz.
        Here is the <a href = "https://www.figma.com/file/b5x0AE3AtsmGPDbRBUMRIfL7/Hifi-mockup-2?node-id=3%3A0" className = "linktofig"> final mockup.</a>
        </p>

        <img id = "Overview" className = "imageK" src = {Over} alt = "Overview"/>
        <h2 className = "section" id = "LessonsK"> Lessons Learned </h2>


        <p className = "bodytext" id = "LastK">
        For the next iteration of this design, I would like to flesh out the functionality of the app.
        Additionally, I feel that it would have been useful to paper prototype before committing time to a hi-fi prototype.
        I found the eye-tracking exercise to be extremely interesting. Despite some missing functionality in our model, it was easy to see
        how the user went about visually processing the app, which led to a few easy changes and conclusions.
        </p>

        <a href = "#navs"><img className = "Top" src = {Top} alt ="To the top button"/></a>
      </div>
    </div>
    );
  }
}

export default Kunduz;
