import React, { Component } from 'react';
import { Route,BrowserRouter as Router, Link } from 'react-router-dom';
import Kunduz from './Kunduz';
import Cct from './Cct';
import Proximus from './Proximus';
import Dogs from './Dogs';
import App from './App';


class Router1 extends Component {
  render () {
    return (
      <Router basename = "/Portfolio" history = "hashHistory" >
        <div>
          <Route exact path = "/" component = {App} />
          <Route exact path = "/#/Kunduz" component = {Kunduz} />
          <Route exact path = "/#/Proximus" component = {Proximus} />
          <Route exact path = "/#/Dog-Sorter" component = {Dogs} />
          <Route exact path = "/#/Cape-Cod-Times-Redesign" component = {Cct} />


        </div>
      </Router>
    )
  }
}

export default Router1;
