import React, { Component } from 'react';
import './Project.css'
import { Link } from 'react-router-dom';


class Project extends Component {



  render () {

    var image_style = {
      backgroundImage: 'url('+this.props.imlink+')',
      width: '100%'
    }

    // correctlink =
    var link = "hello";
    if (this.props.name === "Proximus") {
      link = "/#/Proximus"

    }
    else if (this.props.name === "Cape Cod Times Redesign") {
      link = "/#/Cct";
    }
    else if (this.props.name === "Kunduz Concept") {
      link = "/#/Kunduz";
    }
    else {
      link = "/#/Dogs";
    }






    return (
      <div className = "shape">
        <Link to = {link}><div className = "image" style = {image_style} href = {this.props.pagelink}>
          <img className = "shadow" src = {this.props.imlink} alt = 'blur'/>
        </div></Link>
        <h1 className = "name"> {this.props.name}</h1>
      </div>
    )
  }
}


export default Project;
